<template>
  <div class="inputClass">
    <div class="inputLabel"> {{ label }}</div>
    <div class="input">
      <input class="innerInput" :name="name" :value="value" @input="onInput" @change="onChange" type="text" disabled/>
    </div>
  </div>
</template>

<script>
export default {
  name: "c-input",
  props: {
    label: {
      type: String,
      required: true,
    },
    value: {
      type: String,
      required: true,
    },
    textarea: {
      type: String
    }
  },
  computed: {
    name() {
      // return this.label.toLowerCase();
    },
  },
  mounted() {
    // console.log('this.textarea', this.textarea !== undefined)
  },
  methods: {
    onInput(event) {
      // Can add validation here
      this.$emit('value', event.target.value);
    },
    onChange(event) { // Supports .lazy
      // Can add validation here
      this.$emit('change', event.target.value);
    },
  }
}
</script>

<style lang="scss" scoped>

.inputClass {
  direction: rtl;
  background-color: white;
  border-radius: 0.5rem;
  height: 52px;
  display: flex;
  align-items: center;
  padding: 0 0.75rem;
  font-weight: 700;

  .input {
    width: 60%;
  }

  .innerInput {
    all: unset;
    width: 100%;
  }
}

//TODO: Fix Nested
