<template>
  <div id="home">
    <div class="input">
      <c-input v-model="name" label="نام و نام خانوادگی"/>
    </div>
    <div class="input">
      <c-input v-model="mobile" label="شماره همراه"/>
    </div>
    <div class="input">
      <c-textarea ref="c-textarea" v-model="address" label="آدرس"/>
    </div>
    <div class="changeAddress">
      <c-button @click="changeAddressBtn" :mode="'none'">
        تغییر یا اضافه کردن آدرس جدید
      </c-button>
    </div>
    <div class="action">
      <c-button @click="nextPage" :mode="'primary'" width="70%">
        من درخواست تعمیرکار پکیج دارم
      </c-button>
      <c-button @click="refusePage" class="reqbtn" :mode="'none'" width="30%">
        نه نمیخوام
      </c-button>
    </div>

    <vue-bottom-sheet ref="myBottomSheet" class="vue-bottom-sheet">
      <div class="bottomSheet">
        <div class="bottomSheetAction">
          <!--          <img src="@/assets/Close.svg" alt=""/>-->
          <span class="newAddressTitle">انتخاب آدرس</span>
        </div>
        <div class="radioBox">
          <template v-for="(item, index) in items">
            <c-radio-box @change="changeAddress" class="radioBoxItem" :label="item.label" :value="item.value"
                         v-model="MySelectedValue"/>
          </template>
        </div>
        <c-button class="newAddressBtn" :mode="'secondary'">
          <div @click="selectAddress" class="newAddress">
            <div class="newAddressImg"><img src="@/assets/Plus.svg" alt=""></div>
            <div>آدرس جدید</div>
          </div>
        </c-button>
      </div>
    </vue-bottom-sheet>

  </div>
</template>

<script>
import TheFooter from "@/components/TheFooter";
import CInput from "@/components/c-input";
import CButton from "@/components/c-button";
import CTextarea from "@/components/c-textarea";
import VueBottomSheet from "@webzlodimir/vue-bottom-sheet";
import CRadioBox from "@/components/c-radio-box";

export default {
  name: "Home",
  components: {
    CRadioBox,
    CTextarea,
    CButton,
    CInput,
    VueBottomSheet,
    TheFooter,
  },
  data() {
    return {
      MySelectedValue: "0",
      mobile: "۰۹۹۰۳۵۲۶۸۹۴",
      name: "سید محمدرضا حسینی فرد",
      items: [
        {
          value: "123",
          label: "پردیسان، بلوار شهرند، دانشگاه شهاب دانش"
        },
        {
          value: "456",
          label: "خیابان آذر، شهید محلاتی، کوچه هشت، پلاک دو"
        },
        {
          value: "789",
          label: "انسجام، خیابان شهید محرابی، کوچه بیست و یک، محتمع مسکونی فرهنگ، واحد سی و دو"
        },
      ],
      // address: "ADDRESS",
    }
  },
  mounted() {
    // setTimeout(() => {
    //   this.changeAddress()
    // }, 400)
  },
  computed: {
    address() {
      return this.items.forEach((item) => {
          return item.value == this.MySelectedValue
      })
    }
  },
  methods: {
    selectAddress() {
      this.$refs.myBottomSheet.close()
      this.address = ""
      this.$refs["c-textarea"].resize()
      setTimeout(() => {
        this.$refs["c-textarea"].resize()
        this.$refs["c-textarea"].focus()
        console.log('hi')
      }, 0)
    },
    nextPage() {
      // setTimeout(() => {
      this.$router.push('/theend');
      // }, 0)
    },
    refusePage() {
      // setTimeout(() => {
      this.$router.push('/refuse');
      // }, 0)
    },
    changeAddress(event) {
      console.log('haha !', event)
    },
    changeAddressBtn() {
      this.$refs.myBottomSheet.open()
    }
  }
}
</script>

